.about-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-height: 90vh;
}
.about-photo {
  display: inherit;
  align-items: center;
  margin: 40px 0;
  /* max-height: 100%; */
  /* max-width: 45%; */
  width: 45%;
}
.about-photo img {
  width: 100%;
}
.about-text {
  margin: 40px 0;
  height: 100%;
  width: 45%;
  overflow: hidden;
}
@media screen and (max-width: 768px) {
  .about-photo {
    margin: 40px 0;
    display: none;
  }
  .about-text {
    margin: 40px 0;
    height: 100%;
    width: 100%;
  }
}
