#portfolio {
  min-height: 90vh;
  margin: 40px 0;
}
.portfolio-container {
  display: flex;
}
.portfolio-row {
  display: flex;
  flex-wrap: wrap;
  padding: 15x;
  width: 95%;
}
.work-one {
  display: inherit;
  max-width: 30% !important;
  height: auto;
  margin: 10px 10px;
}
.work-one i {
  color: #0097a7;
  margin-top: 1em;
}
.card-image {
  max-height: 180px;
  overflow: hidden;
}
@media screen and (max-width: 768px) {
  .portfolio-row {
    display: flex;
    flex-direction: column;
  }
  .work-one {
    margin: 10px 0 0 0 !important;
    min-width: 100%;
  }
}
