.services {
  height: 80vh;
  margin: 50px 0;
  max-width: 100%;
}
.services-container {
  display: flex;
  align-items: center;
}
.services-row {
  display: flex;
  padding: 15x;
  width: 95%;
  height: 70%;
}
.service-one {
  display: inherit;
  margin: 0 10px;
}
.service-one i {
  color: #0097a7;
  margin-top: 1em;
}

.card-title {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
}
.stack-icons img {
  width: 48px;
  height: 48px;
}
@media screen and (max-width: 768px) {
  .services {
    height: auto;
  }
  .services-title {
    max-width: 100% !important;
    margin: 0 !important;
  }
  .services-row {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }
  .service-one {
    width: 100% !important;
    margin-left: 0 !important;
    margin-top: 10px;
  }
}
